@media screen and (min-width: 480px ) and (max-width: 800px)
{
  .font-normal
  {
    font-size: 14px;
  }

  .font-smal
  {
    font-size: 10px;
  }

  .font-big
  {
    font-size: 32px;
  }

  .font-humungus
  {
    font-size: 50px;
  }

  html {
    font-size: 14px;
    scroll-behavior: smooth;
  }
  .third {
    width: 80%;
    height: 25%;
    float: left;
    margin: 4%;
  }

  .line {
    display: none;
  }
}
@media screen and (min-width: 800px) and (max-width: 1280px)
{
  
  .font-normal
  {
    font-size: 16px;
  }

  .font-smal
  {
    font-size: 12px;
  }

  .font-big
  {
    font-size: 40px;
  }

  .font-humungus
  {
    font-size: 70px;
  }

  html {
    font-size: 16px;
    scroll-behavior: smooth;
    width: 100vw;
  }

  .third {
    width: 80%;
    height: 30%;
    float: left;
    margin: 4%;
  }
  .line {
    display: none;
  }
}

@media screen and (min-width: 1280px)
{
  .font-normal
  {
    font-size: 24px;
  }

  .font-smal
  {
    font-size: 20px;
  }

  .font-big
  {
    font-size: 50px;
  }

  .font-humungus
  {
    font-size: 90px;
  }

  html {
    font-size: 24px;
    scroll-behavior: smooth;
  }
  .third {
    width: 25%;
    height: 80%;
    float: left;
    margin: 4%;
  }
  .line {
    width: 60vw;
    height: 1.5vh;
    border-radius: 1vw;
    background-color: black;
    box-shadow: #3A0CA3 8px 8px;
    position: absolute;
    margin: 2%;
  }
}

section {
  width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
  overflow-y: hidden;
    overflow-x: hidden;
}


body {
  overflow-x: hidden;
  background-color: white;
  font-family: monospace;
  scroll-behavior: smooth;
}

.welcome {
  text-align: center;
  padding: 5%;
  color: black;
  margin: 0;
  text-decoration: underline #3A0CA3 1rem;
  z-index: 10;
}

.logoContainer {
  position: absolute;
  top: 0;
  left: 0;
  display: grid;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  z-index: -1;
}

.biglogo {
  height: 40vh;
}
.Nav {
  padding: 2%;
    text-decoration: underline #3A0CA3;
}

.navbox {
  position: absolute;
  min-width: 20%;
  bottom: 5%;
  left: 2%;
}
.lineBottom {
  right: -30vw;
  bottom: 0;
}
.lineTop {
  left: -30vw;
  top: 0;
}
.TitleLink {
  color: black;
  text-decoration: none;
  margin: 1% 5%;
  transition: all 0.2s ease-in-out;
}

.TitleLink:hover {
  margin: 1% 7%;
  cursor: pointer;
  font-weight: 4 00;
  transition: all 0.2s ease-in-out;
  text-decoration: underline #3A0CA3 5px;
}

.SectionTitle {
  color: black;
  padding: 2% 0%;
    margin: 2%;
  display: inline;
  position: relative;
  text-decoration: underline #3A0CA3 solid;
}
.half {
  width: 50%;
  margin: 10% 25%;
  vertical-align: middle;
}



.container {
  width: 80%;
  height: 60%;
  margin: 5% 10%;
}

.cardcontainer {
  width: 80%;
  height: 60%;
  margin: 5% 10%;
}
.text-center {
  text-align: center;
}

b {
  color: #3A0CA3;
}

a {
  text-decoration: underline #3A0CA3;
}

a:hover {
  cursor: pointer;
}

.linkbutton {
  padding: 2%;
  min-width: 20%;
  max-width: 30%;
  border: 2px solid black;
  color: black;
  text-decoration: none;
  text-align: center;
  transition: all 0.2s ease;
  box-shadow: #4361EE 8px 8px;
}

.linkbutton:hover {
  background-color: black;
  color: white;
  transition: all 0.2s ease;
  box-shadow: #7209B7 8px 8px;
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.card {
  transform-style: preserve-3d;
  transition: all 0.5s ease;
}

.card:hover {
  cursor: pointer;
}

.card_rotate {
  transform: rotateY(180deg);
}

.cardfront {
  position: absolute;
  border: 2px solid black;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  background-color: white;
  backface-visibility: hidden;
  color: black;
  position: relative;
}

.semi-background {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  padding: 4%;
  box-sizing: border-box;
  margin-top: 10%;
}
.cardback {
  position: absolute;
  border: 2px solid black;
  top: 0;
  left: 0;
  width: 100%;
  background-color: black;
  height: 100%;
  background-size: cover;
  box-sizing: border-box;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  color: white;
  text-align: justify;
  transform: rotateY(180deg);
}

.cardtitle {
  text-align: center;
}

.flip {
  position: absolute;
  bottom: 0;
  right: 0;
}

.personhexagon {
  width: 18vw;
  height: 20vw;
  margin: 1vw;
  position: relative;
  display: inline-block;
  background-color: transparent;
}

.personhexagon:hover {
  cursor: pointer;
}

.hexagonbackground {
  width: 100%;
  height: 100%;
  z-index: -2;
  position: absolute;
  top: 0;
  left: 0;
}

.persontitle {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 20%;
  text-align: center;
  z-index: 1;
  color: white;
  background-color: black;
}

.hrow {
  margin-left: 10vw;
  width: 60vw;
}

.hexagonrow2 {
  margin-top: -5vw;
  padding-left: 10vw;
}

.portrait {
  height: 27vh;
  max-height: 15vw;
  position: absolute;
  bottom: 30%;
  left: 30%;
}

.socials {
  margin-left: 20%;
  width: 60%;
  display: flex;
  justify-content: space-around;
  height: 100%;
}

.social {
  max-height: 10vw;
  max-width: 10vw;
}

.imglink {
  height: 100%;
}

.cardbottom {
  color: white;
  width: 60%;
  height: 5%;
  position: absolute;
  bottom: 5%;
  left: 20%;
  text-align: center;
  text-decoration: none;
  border: 4px solid white;
}
.colorful-top {
  z-index: -1;
  width: 6vw;
  height: 6vw;
  background-color: #7209B7;
  position: absolute;
  top: -2vw;
  left: -2vw;
}

.colorful-bot {
  z-index: -1;
  position: absolute;
  width: 6vw;
  height: 6vw;
  background-color: #4361EE;
  bottom: -2vw;
  right: -2vw;
}